.page-ar .archive-content {
  display: block;
}

.read-entry {
  border: 1px solid black;
  color: black;
  width: 10em;
  text-align: center;
  padding: 0.75rem 1.5rem;
  font-size: 15px;
  text-decoration: none;
  text-transform: uppercase;
  &:hover {
    background: #248a83;
    border: none;
    color: white;
  }
}
.entry-content {
  padding: 0;
}

.archive.date {
  .content-area {
    padding-right: 10rem;
  }

  .entry-header {
    border: unset;
    position: relative;

    .entry-title {
      position: absolute;
      top: 0;
      left: 0;
      background-color: #248a83;
      height: 5em;
      width: 24em;
      padding: 0.8em;

      a {
        text-decoration: none;
        color: white;
        font-size: 30px;
      }
    }
    .entry-meta {
      position: absolute;
      bottom: 7px;
      right: 0;
      background-color: #248a83;
      height: 2em;
      color: white;
      text-transform: uppercase;
      padding: 0.2em 1em;
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

